export default "@import '../../../../themes/src/default.css';\n@import '../../../../calendar/src/vars.css';\n\n:root {\n    --calendar-input-icon-color: var(--color-light-graphic-primary);\n    --calendar-input-popover-border-radius: var(--border-radius-m);\n}\n\n.component {\n    display: inline-block;\n    outline: none;\n    position: relative;\n}\n\n.block {\n    width: 100%;\n}\n\n.calendarContainer {\n    display: inline-block;\n    box-sizing: border-box;\n    border-radius: var(--calendar-input-popover-border-radius);\n    border: 1px solid var(--color-light-border-secondary);\n\n    @media (max-width: 374px) {\n        width: 100%;\n        min-width: 288px;\n    }\n}\n\n.calendarResponsive {\n    width: var(--calendar-width);\n    padding: 0 var(--gap-m);\n}\n\n.calendarIcon {\n    width: 24px;\n    height: 24px;\n    display: block;\n    color: var(--calendar-input-icon-color);\n\n    &:not(:only-child) {\n        margin-right: var(--gap-2xs);\n    }\n}\n\n.nativeInput {\n    opacity: 0;\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    appearance: none;\n    z-index: 1;\n\n    &::-webkit-calendar-picker-indicator {\n        display: none;\n    }\n    &::-webkit-inner-spin-button {\n        display: none;\n    }\n}\n";